import Vue from "vue";
import VueRouter from "vue-router";
import Login from "@/views/Login.vue";
import Flowers from "@/views/Flowers.vue";
import Welcome from "@/views/Welcome.vue";
import Swipers from "@/views/Swipers.vue";
import Message from "@/views/Message.vue";
import Write from "@/views/Write.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Login",
    component: Login
  },
  {
    path: "/flowers",
    name: "Flowers",
    component: Flowers
  },
  {
    path: "/welcome",
    name: "Welcome",
    component: Welcome
  },
  {
    path: "/swipers",
    name: "Swipers",
    component: Swipers
  },
  {
    path: "/message",
    name: "Message",
    component: Message
  },
  {
    path: "/write",
    name: "Write",
    component: Write
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
