<template>
  <div class="item" :class="'item'+ index">
    <img
      class="leslieImage"
      :src="leslieImage"
      :style="{width: width + 'rem',height: height + 'rem'}"
    />
    <div class="left">
      <div class="top-audio" v-if="index === 0">
        <img class="audioa" src="@/assets/image/audio-a.png" alt />
        <img class="audiob" src="@/assets/image/audio-b.png" alt />
      </div>
      <div class="top-swiper" v-else>
        <van-swipe class="swiper" :show-indicators="false" :autoplay="3000">
          <van-swipe-item v-for="(i, idx) in imageList" :key="idx">
            <van-image
              width="5rem"
              height="4.3rem"
              fit="contain"
              :src="i"
              @click="preview(idx)"
            />
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="bottom">{{ Introduction }}</div>
    </div>
    <div class="right">十七周年继续宠爱张国荣</div>
  </div>
</template>

<script>
import { Swipe, SwipeItem, Image, ImagePreview } from "vant";
import "swiper/css/swiper.css";

export default {
  props: [
    "index",
    "leslieImage",
    "width",
    "height",
    "Introduction",
    "imageList"
  ],
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Image.name]: Image,
    [ImagePreview.name]: ImagePreview
  },
  methods: {
    preview(i) {
      ImagePreview({
        images: this.imageList,
        startPosition: i
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.item {
  position: relative;
  width: 100%;
  height: fill-available;
  display: flex;
  background-color: rgba(0, 0, 0);

  .leslieImage {
    z-index: 2;
    position: absolute;
    right: 0;
    bottom: 0;
  }
  .left {
    z-index: 3;
    width: 70%;
    @include flex(flex-start, flex-start, column, nowrap);
    .top-audio {
      position: relative;
      height: 4.3rem;
      .audioa {
        position: absolute;
        top: 0.66rem;
        left: 0.5rem;
        width: 3.61rem;
        height: 3.61rem;
        animation: rotateAudio 5s linear 0 infinite normal none;
      }
      .audiob {
        position: absolute;
        top: 0;
        left: 2.13rem;
        width: 1.37rem;
        height: 1.81rem;
      }
    }
    .top-swiper {
      width: 100%;
      margin-left: 0.5rem;
      margin-top: 0.5rem;
      height: 4.3rem;
    }
    .bottom {
      z-index: 3;
      margin-top: 1rem;
      margin-left: 0.5rem;
      height: 6rem;
      letter-spacing: 0.05rem;
      writing-mode: vertical-rl;
      @include font-style(0.28rem, #fdfafa, 400);
    }
  }
  .right {
    z-index: 3;
    margin-top: 0.66rem;
    line-height: 2.25rem;
    letter-spacing: 0.1rem;
    writing-mode: vertical-lr;
    @include font-style(0.42rem, #fff, 600);
    -webkit-text-stroke: 0.02rem #65440c;
  }
}
@keyframes rotateAudio {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>